import React, {Component, Suspense} from "react";
import "firebase/database";
import 'firebase/storage';
import MySnackbarContentWrapper from "../../tools/customSnackBar";
import Snackbar from "@material-ui/core/Snackbar";
import moment from "moment";
import "moment/locale/fr"
import Loader from "../../components/Loader";
import baseline from "../../assets/images/smartDom/baseline.png"
import ether from "../../assets/images/smartDom/ether.png"
import smartco from "../../assets/images/logos/logoSmartCo.jpeg"
import chart from "../../assets/images/smartDom/chart.svg"
import integration from "../../assets/images/smartDom/integration.svg"
import echange from "../../assets/images/smartDom/echange.svg"
import wallet from "../../assets/images/smartDom/wallet.svg"
import tokinization from "../../assets/images/smartDom/tokenization.svg"
const Topbar = React.lazy(() => import("../../components/Topbar"));
const loading = () => <Loader/>

moment.locale('fr');


class  tokenizeSmartco extends Component {



    constructor(props) {
        super(props);



        this.state = {
            modalAssistance: false,
            modalWebCoferance: false,
            loading: false,
            isMenuOpened: false,
            activeMenuItem: 'item-gestion',
            openAlert: false,
            alertMessage: '',
            alertType: '',

            savoirplus:false


        }

    }





    render() {



        return (





            <div className="bg-dark" >


                <header id="topnav" >
                    <Suspense fallback={loading()}>
                        <Topbar changeActivePage={this.changeActivePage}/>
                    </Suspense>
                </header>
                <div  className="wrapper " style={{backgroundColor:"#ffffff"}} >

                    <Suspense fallback={loading()}>
                        {this.state.loading && <Loader/>}





                        <div className="container-fluid " >

                            <div className="row align-items-center">
                                <div className="col-md-10">
                                    <h2 >
                                        Grce  notre engagement au sein de lEEA, et Baseline et un acteur genevois , nous travaillons activement sur les normes et faonnons l'avenir de l'adoption de la blockchain dentreprise,

                                    </h2>

                                </div>
                                <div className="col-md-1">
                                    <img src={baseline} style={{width:"100%"}}/>

                                </div>
                                <div className="col-md-1">
                                    <img src={ether} style={{width:"100%"}}/>

                                </div>

                            </div>

                            <div className="row mt-4 align-items-center">

                                    <img src={smartco} style={{width:"8%",marginLeft:"10%"}}/>


                                <div className="col-md-4">
                                    <text style={{fontSize:17 ,color:"black"}}>
                                        Avec loffre  TOKENIZE de SMARTCO  nous avons tout en place pour distribuer et grer les devises numriques et les actifs tokeniss
                                    </text>

                                </div>

                            </div>
                            <div className="row mt-4 align-items-start">

                                <img src={chart} style={{width:"8%",marginLeft:"10%"}}/>


                                <div className="col-md-8">
                                    <div>
                                    <text style={{fontSize:17 ,color:"black"}}>
                                        La tokenisation est le processus de reprsentation de la titrisation d'un actif du monde rel  l'aide de jetons numriques sur une blockchain
                                    </text>
                                    </div>
                                    <div className="mt-2">
                                        <text style={{fontSize:17 ,color:"black"}}>
                                            C'est un cas dutilisation significatif  de la technologie blockchain, comportant une partie d'intgration importante. Nous proposons des conseils et des solutions toutes faites dans tous les domaines ncessaires  notre vision d'une plate-forme de jetons de scurit  360  conforme  la rglementation, y compris des solutions de liquidit et des services de garde .
                                        </text>
                                    </div>
                                    <div className="mt-2">
                                        <text style={{fontSize:17 ,color:"black"}}>
                                            Une plate-forme de jetons de scurit regroupe mission, un march conforme ( compliance. )   la rglementation FINMA et liquidit garantie  l'intrieur de la plateforme.
                                            Marketplace
                                        </text>
                                    </div>



                                </div>

                            </div>





                        </div>

                        {this.state.savoirplus===true &&
                        <div className="container-fluid mt-5">
                            <div>
                                <h3>
                                    Smartco propose  un processus d'mission indpendant de la plateforme ciblant l'expert du domaine (par exemple, un propritaire d'actif immobilier), et non un expert juridique et / ou technique
                                </h3>
                            </div>

                            <div className="row align-items-center">
                                <div className="col-md-2 ">
                                    <img src={smartco} style={{width:"80%"}}/>
                                </div>
                                <div className="col-md-6">
                                    <h4>
                                        Smartco et ses partenaires offrent :
                                    </h4>

                                </div>

                            </div>

                            <div className="row justify-content-center mt-4" >
                                <div className="col-md-1">
                                    <img src={chart}style={{width:"100%"}}/>

                                </div>
                                <div className="col-md-3">
                                    <h4 className="font-weight-bold">Une plate-forme devrait de manire transparente </h4>
                                </div>
                                <div className="col-md-5">
                                    <text style={{fontSize:17}}>
                                        Permettre des flux de revenus  long terme tels que les frais de ngociation, introduire de nouvelles classes d'investissement (par exemple pour les fournisseurs de liquidit) et inclure toutes les parties impliques pour bnficier de la compensation des frais de ngociation.

                                    </text>
                                </div>

                            </div>
                            <div className="row justify-content-center mt-3" >
                                <div className="col-md-1">
                                    <img src={echange}style={{width:"100%"}}/>

                                </div>
                                <div className="col-md-3">
                                    <h4 className="font-weight-bold">changes et liquidit  </h4>
                                </div>
                                <div className="col-md-5">
                                    <text style={{fontSize:17}}>
                                        Nous proposons un partenaire, freeport, qui offre diffrentes options pour excuter un change de jetons de scurit conforme  la rglementation avec une liquidit automatise
                                    </text>
                                </div>

                            </div>
                            <div className="row justify-content-center mt-3" >
                                <div className="col-md-1">
                                    <img src={integration}style={{width:"100%"}}/>

                                </div>
                                <div className="col-md-3">
                                    <h4 className="font-weight-bold">L'intgration  </h4>
                                </div>
                                <div className="col-md-5">
                                    <text style={{fontSize:17}}>
                                        Nous proposons une intgration au niveau technique (par exemple, gnration de contrats intelligents, connexion de systmes ERP), au niveau du processus (par exemple, fourniture de workflows pour dmarrer un processus) et galement au niveau du march (par exemple, concepts de liquidit)                                    </text>
                                </div>

                            </div>
                            <div className="row justify-content-center mt-3" >
                                <div className="col-md-1">
                                    <img src={wallet}style={{width:"100%"}}/>

                                </div>
                                <div className="col-md-3">
                                    <h4 className="font-weight-bold">Drivs et portefeuilles
                                    </h4>
                                </div>
                                <div className="col-md-5">
                                    <text style={{fontSize:17}}>
                                        Nous proposons des portefeuilles d'missions de drivs et de logiciels bass sur des rgles, ngociant automatiquement les actifs sousjacents
                                    </text>
                                </div>

                            </div>
                            <div className="row justify-content-center mt-3" >
                                <div className="col-md-1">
                                    <img src={tokinization}style={{width:"100%"}}/>

                                </div>
                                <div className="col-md-3">
                                    <h4 className="font-weight-bold">Tokenisation en tant que service
                                    </h4>
                                </div>
                                <div className="col-md-5">
                                    <text style={{fontSize:17}}>
                                        Nous proposons des forfaits sur mesure pour rpondre aux besoins de chaque client
                                    </text>
                                </div>

                            </div>

                        </div>
                        }




                        <div className="container-fluid mt-4 align-items-center">
                            <div className="row justify-content-around">
                                <div className="col-md-3">
                                    {this.state.savoirplus===false &&
                                    <h5 onClick={()=>this.setState({savoirplus:true})}>
                                        <text style={{cursor:"pointer"}}><u>En savoir plus</u></text>
                                    </h5>
                                    }

                                </div>
                                <div>
                                    <button
                                        style={{width:"120%",backgroundColor: "#0c68ca", borderColor: "#0c68ca",borderRadius:100}}
                                        className="btn btn-small  justify-content-start">
                                        <text style={{ fontWeight: "bold", color: "white"}}>
                                            Mise en relation avec SmartCo
                                        </text>
                                    </button>
                                </div>

                            </div>
                        </div>















                        <Snackbar
                            anchorOrigin={{
                                vertical: 'bottom',
                                horizontal: 'left',
                            }}
                            open={this.state.openAlert}
                            autoHideDuration={5000}
                            onClose={this.closeSnackbar}
                        >
                            <MySnackbarContentWrapper
                                onClose={this.closeSnackbar}
                                variant={this.state.alertType}
                                message={this.state.alertMessage}
                            />
                        </Snackbar>

                    </Suspense>

                </div>




            </div >



        )
    }


}


export default tokenizeSmartco;
